.quoteswidget{
    position: relative;
    display: inline-block;
    background-color: blue;
    border-radius: 40px;
    width: 400px;
    max-height: 300px;
    margin: 20px;
}

.quote{
    color:white;
    margin: 10px;

}

.close{
    background-color: rgb(250, 84, 62);
    border-radius: 100px;
    position:absolute;
    left: 2px;
    margin: 10px;
    z-index: -1;
}


.quoteswidget:hover .close{
    z-index: 1;
}
.quoteswidget { 
   transition: all .2s ease-in-out; 
   }
   
   .quoteswidget:hover { 
   transform: scale(1.1); 
   }