.pomodorowidget {
    position: relative;
    margin: 20px;
    display: inline-block;
    background-color: #ffe0c4;
    border-radius: 40px;
 }


 .close{
     background-color: rgb(250, 84, 62);
     border-radius: 100px;
     position:absolute;
     left: 2px;
     margin: 10px;
     z-index: -1;
 }

.sectionTitle{
    font-size: 20px;
    margin: 40px;
    margin-top: 20px;
    border: solid 1px;
    padding: 3px;
    border-radius: 10px;
    color: #d95d6c;
}

.timers{
    font-size:20px; 
    margin:20px;
    color: #234d57
}


.controls{
    border-radius: 20px;
    background-color:#ff6e63;
}


 .pomodorowidget:hover .close{
     z-index: 1;
 }
 .pomodorowidget { 
    transition: all .2s ease-in-out; 
    }
    
    .pomodorowidget:hover { 
    transform: scale(1.1); 
    }
