.meditationwidget {
    position: relative;
    display: inline-flex;
    margin: 20px;
    border-radius: 40px;

 }

 .close{
     background-color: rgb(250, 84, 62);
     border-radius: 100px;
     position:absolute;
     margin: 10px;
     z-index: -1;
 }

 .duration{
    color: white;
    position:absolute;
    margin: 10px;
    top: 30%;
 }

 .duration input{
     color: white;
     height: 70px;
     width: 70px;
     text-align: center;
     font-size: 50px;
     background: transparent;
 }



 .startButton{
     position: absolute;
     top: 45%;
     left: 30%;
     border:none;
     background: transparent;
     
 }

 .startButton :hover{
    border-radius: 20px;
     background-color: rgb(50, 141, 168);
 }

 .timer{
     position: absolute;
     top: 25%;
     left: 8%;
     color: white;
     
 }


 .meditationwidget:hover .close{
    z-index: 1;
}
.meditationwidget { 
   transition: all .2s ease-in-out; 
   }
   
   .meditationwidget:hover { 
   transform: scale(1.1); 
   }