.todowidget {
    position: relative;
    margin: 20px;
    display: inline-block;
    background-color: white;
    border-radius: 40px;
 }



.i_button{
    background-color: #00f0fc;
    border-radius: 10px;
    margin: 20px;
}


 .close{
    background-color: rgb(250, 84, 62);
    border-radius: 100px;
    border:none;
    position:absolute;
    left: 2px;
    margin: 10px;
    z-index: -1;
}

ul li {
    line-height: 1.5;
    padding: 0.5em 0;
    list-style-type: none!important;
  }





.todowidget:hover .close{
    z-index: 1;
}
.todowidget { 
   transition: all .2s ease-in-out; 
   }
   
   .todowidget:hover { 
   transform: scale(1.1); 
   }